.lc{
	padding-bottom: 100px;
	.container{
		display: flex;
		flex-wrap: wrap;
	}
	&-content{
		width: calc(100% - (345px + 25px));
		margin-left: 25px;
	}
	.catalog-container{
		margin-top: 0;
	}
}

.lc-content__bottom-btns{
	width: 100%;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	align-items: center;
	margin-bottom: 15px;
	.repeat-order{
		border-color: #4D585A;
		color: #4D585A;
		&:hover{
			border-color:  #EF4949;
		}
	}
	.state{
		span{
			margin-left: 4px;
			color: #73AF00;
		}
		.red{
			color :#EF4949;
		}
	}
	> div{
		display: flex;
		align-items: center;
	}
	.sbmt {
		margin-left: 22px;
		padding: 12px 30px;
		box-shadow: none;
		border: 2px solid #EF4949;
		border-radius: 5px;
		color: #EF4949;
		background-color: transparent;
		transition: .3s;
		cursor: pointer;
		font-family: "montserat";
		&:hover {
			background-color: #EF4949;
			color: #fff;
		}
	}
}

.input-text-check{
	position: relative;
	.check--absolute{
		position: absolute;
		right: 0;
		bottom: 10px;
	}
	.jq-checkbox.checked{
		background-color: #EF4949;
		border-color: #EF4949;
	}
}
.input-text-label{
	label{
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		width: 100%;
	}
	.jq-file__browse, .jq-file__name{
		font-family: "montserat";
	}
	.jq-file__name{
		height: 100%;
		border: 0;
		box-shadow: none;
		border-radius: 0;
		outline: none !important;
		border: 0 !important;
		padding-left: 0;
	}
	.jq-file {
		width: 100%;
		border-radius: 0;
		box-shadow: none;
		border-bottom: 1px solid #3A3A3A;
		padding: 3px 15px 7px;
		position: relative;
		&:hover .jq-file__browse {
			background: none;
			background-color: #EF4949;
			color: #fff;
		}
		input {
			width: 100%;
			height: 100%;
			position: absolute;
			font-size: 0;
		}
	}
	.jq-file__browse{
		border: 2px solid #EF4949 !important;
		border-radius: 5px !important;
		color: #EF4949;
		height: auto;
		bottom: 6px;
	}
	.jq-file__browse {
		position: absolute;
		top: 1px;
		right: 1px;
		padding: 0 10px;
		border-left: 1px solid #9e9e9e;
		border-radius: 0;
		background: none;
		box-shadow: none;
		text-shadow: none;
	}
}




@media screen and(max-width: 990px){
	.lc-content__bottom-btns .sbmt{
		padding: 5px 10px;
	}
}
@media screen and(max-width: 600px){
	.lc-content__bottom-btns > div{
		width: 100%;
		margin-top: 15px;
		flex-wrap: wrap;
	}
	.lc-content__bottom-btns .sbmt{
		width: 100%;
		margin-top: 15px;
		margin-left: 0;
        padding: 10px;
	}
	.input-text-check .check--absolute{
		bottom: 37px;
	}
	.input-text-label .jq-file{
		margin-bottom: 30px;
	}
}